$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.container {
  .header {
    display: flex;
    justify-content: flex-end;
    padding: 8px;
    background-color: #fff;
  }
}
